import React from 'react';
import { Footer as Jumbo, Container, Nav, Navbar } from 'react-bootstrap';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

const Styles = styled.div`
  .navbar {
      background: #232323;  /* fallback for old browsers */
  }
  a, .navbar-nav .nav-link {
      font-size: 16px;
      color: white;
      line-height: 44px;
    &:hover {
      color: #212529;
      text-decoration: none;
    }
  }
  h3 {
    font-size: 14px;
    font-weight: 300;
    color: white;
    margin: auto;
    padding: 10px;
  }
}
`;

export const Footer = () => (
  <Styles>
   <Navbar expand="lg">
    <Container>
            <h3>© 2020 Martin's Portfolio. <br /> All Rights Reserved.</h3>
    </Container>
  </Navbar>
  </Styles>
)

